// Apply a natural box layout model to all elements
// but allowing components to change
// http://www.paulirish.com/2012/box-sizing-border-box-ftw/
html {
    box-sizing: border-box;
    height: 100%;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100%;
    margin: 0;
    padding: 0;
    background-color: #24292e;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#bg-fullscreen {
    top: 0;
    left: 0;
    min-height: 100%;
    min-width: 100%;
    width: auto;
    height: auto;
    display: block;
    position: fixed;
    z-index: -999;
    opacity: 0;
    transition: opacity 2s;
}

.content {
    text-align: center;
    transform: translateY(-5%);
}

.avatar {
    display: inline-block;
    border-radius: 50%;
    border: 4px solid #ffffff;
    transition: transform 500ms ease-in-out;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
    background-image: url(https://www.gravatar.com/avatar/52869e2103531ad458ab57bf53b29679?s=280);
    background-size: cover;
    width: 140px;
    height: 140px;

    img {
        display: none;
    }
}

.title {
    h2 {
        font-size: 22pt;
        font-weight: 200;
        letter-spacing: 2px;
        color: #ffffff;
        margin: 30px 0 20px 0;
    }
}

.social-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    a {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin: 0 5px;
        text-decoration: none;
        transition: background-color 300ms ease-in;
        line-height: 0; 
        font-size: 0;
        color: transparent;

        i {
            font-size: 26pt;

            &.icon-medium {
                font-size: 24pt;
            }
            &.icon-unsplash {
                font-size: 21pt;
            }
            &.icon-tumblr {
                font-size: 21pt;
            }
            &.icon-soundcloud {
                font-size: 24pt;
            }
        }

        span {
            line-height: 0; 
            font-size: 0;
            color: transparent; 
        }
    }
}

@media only screen and (min-width:961px)  {
    .avatar:hover {
        transform: rotate(360deg);
    }

    .social-icons a:hover {
        transition: background-color 50ms ease-in;
        background-color: rgba(255, 255, 255, 0.1);
    }
}
